<template>
  <div>
    <v-card dark>
      <div class="text-no-wrap secondary">
           <v-app-bar
           rounded
            flat
            color="#FF7F24"
            
          >
            <v-toolbar-title >
              Pfam results
            </v-toolbar-title>
            <v-spacer></v-spacer>

            <v-btn
              color="white"
              icon @click = goToAlignment
            >
              <v-icon>mdi-arrow-up-bold-box-outline</v-icon>
            </v-btn>
          </v-app-bar>
        </div>
      <v-card-title></v-card-title>
      <v-card-text>
        Pfam is a protein familiy database that classifies protein sequences into families, domains and clans. Here, Pfam predicts regions that contain domains in the found protein.
        <a href="https://pfam-legacy.xfam.org/" target="_blank">Go to Pfam <v-icon>mdi-open-in-new</v-icon></a>
        <div>
          <v-data-table
            dense
            dark
            style="margin: 5px"
            :headers="headers"
            :items="pfam_info"
            :items-per-page="10"
            class="elevation-1"
            :single-expand="singleExpand"
            :expanded.sync="expanded"
            item-key="startposition"
            show-expand
          >
            <template v-slot:expanded-item="{ headers, item }">
              <td :colspan="headers.length">
                Alignment of {{ item.name }}:   . = insert of sequence, - = insert of HMM, + = similar residues, * = best probability (10)
                 <pre
                style="normal 14px/16px 'Courier New', Courier, Monospace; overflow-x: auto"
                >  hmm: {{item.hmm}}
match: {{item.match}}
 prob: {{item.probability}}
                 </pre>
              </td>
            </template>

            <template #item.link="{item}">
              <td><a :href="item.link" target="blank">{{ item.link }}</a></td>
            </template>

            <template #item.domain_in_extension="{item}">
              <td :style="customRowStyle(item)">{{item.domain_in_extension}}</td>
            </template>
          </v-data-table>
        </div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      headers: [],
      pfam_info: [],
      expanded: [],
      singleExpand: false,
    };
  },
  props: {
    proteinId: {
      required: true,
      type: String,
    },
  },
  methods: {
    customRowStyle(item) {
      if (item.domain_in_extension == "in extension" || item.domain_in_extension == "overlap") {
        return "background-color: #FF7F24; color: #000000;";
      }
      return "";
    },
    goToAlignment(){
      this.$emit("callParent");
    }
  },
  mounted() {
    const projectID = this.$route.params.projectID;
    let project_information = this.$store.state.data;
    const project = project_information[projectID];
    const list_of_info = project.fragment_information;
    const id = parseInt(this.$route.params.id);
    const entry = list_of_info.find((x) => x.id === id);
    let pfam_info_json = entry["pfam_info"];
    let pfam_info = [];
    let found_pfam_hits = [];
    for (let result of pfam_info_json) {
      if (result.length != 0) {
        let accession = result[2];
        // https://identifiers.org/pfam:PF13499
        // https://www.ebi.ac.uk/interpro/entry/pfam/PF13499/
        if(accession.includes(".")){
          accession = accession.split(".")[0]
        }
        let link = "https://www.ebi.ac.uk/interpro/entry/pfam/" + accession;
        //let link = "https://pfam.xfam.org/family/" + accession;
        const startposition = result[7];
        const endposition = result[8];
        if (!(found_pfam_hits.includes(startposition + ".." + endposition))){
          found_pfam_hits.push(startposition + ".." + endposition);
          pfam_info.push({
            proteinID: result[0],
            name: result[1],
            accession: accession,
            evalue: result[3],
            description: result[4],
            type: result[5],
            clan: result[6],
            startposition: startposition,
            endposition: endposition,
            hmm: result[9],
            match: result[10],
            probability: result[11],
            link: link,
            domain_in_extension: result[13],
          });
        }
      }
    }
    this.pfam_info = pfam_info;

    this.headers = [
      {
        text: "ID",
        align: "start",
        sortable: false,
        value: "proteinID",
      },
      { text: "name", value: "name" },
      { text: "accession", value: "accession" },
      { text: "evalue", value: "evalue" },
      { text: "description", value: "description" },
      { text: "type", value: "type" },
      { text: "clan", value: "clan" },
      { text: "startposition", value: "startposition" },
      { text: "endposition", value: "endposition" },
      /* { text: "hmm", value: "hmm" },
      { text: "match", value: "match" },
      { text: "probability", value: "probability" }, */
      { text: "link", value: "link" },
      {text: "domain in extension", value: "domain_in_extension"},
      { text: 'alignment', value: 'data-table-expand' },
    ];
  },
};
</script>