<template>
  <v-img
    :src="currentImage"
    height="100%"
    contain
  ></v-img>
</template>

<script>
export default {
  data() {
    return {
        images: [
          'data:image/png;base64,',
          'data:image/png;base64,',
          ' data:image/png;base64,',  
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',
          ' data:image/png;base64,',  
          
        ],
        // Speed in milliseconds
        speed: 500,
        currentImage: null,
        currentIndex: 0,
    };
  },
  mounted() {
    this.currentIndex = 0;
    this.currentImage = this.images[this.currentIndex];
    //console.log(this.currentImage);
    setTimeout(this.moveToNextImage, this.speed);
  },
  methods: {
    moveToNextImage() {
        this.currentIndex = (this.currentIndex + 1) % this.images.length;
        this.currentImage = this.images[this.currentIndex];
        setTimeout(this.moveToNextImage, this.speed);
    }
  }
};
</script>
