<template>
  <div>
    <v-card dark>
      <div class="text-no-wrap secondary">
        <v-app-bar rounded flat color="#808080">
          <v-toolbar-title>
            MaxQuant: Intensities of peptide and protein
          </v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn color="white" icon @click="goToAlignment">
            <v-icon>mdi-arrow-up-bold-box-outline</v-icon>
          </v-btn>
        </v-app-bar>
      </div>
      <v-card-title></v-card-title>
      <v-card-text>
        By selecting the protein intensity in the legend, you can add the
        intensities of the whole protein to the chart. Intensities are shown as
        summed up eXtracted Ion Current (XIC) of all isotopic clusters
        associated with the identified AA sequence.
        <div
          ref="bar"
          style="padding: 2%; width: 100%; height: 600px; position: relative"
        ></div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data() {
    return {
      headers: [],
      mqinfo: [],
    };
  },
  mounted() {
    const projectID = this.$route.params.projectID;
    let project_information = this.$store.state.data;
    const project = project_information[projectID];
    const list_of_info = project.fragment_information;
    const id = parseInt(this.$route.params.id);
    const entry = list_of_info.find((x) => x.id === id);
    let category = [];
    let data = [];
    let intensities = entry["intensities"];
    intensities = intensities
      .replace("{", "")
      .replace("}", "")
      .replaceAll("'", "");
    intensities = intensities.replace(/ /g, "");
    intensities = intensities.split(",");
    let proteinGroup_info = entry["proteinGroup_info"];
    let proteinGroupIntensities = proteinGroup_info
      .split("intensity_info': [")[1]
      .split("]")[0]
      .replaceAll("'", "")
      .split(",");
    // console.log(proteinGroupIntensities);

    for (let item of intensities) {
      let x = item.split(":");
      category.push(x[0]);
      data.push(x[1]);
    }

    this.echartBarplot = echarts.init(this.$refs["bar"], "dark");

    const option = {
      title: {
        text: "samples",
        top: "bottom",
        left: "center",
      },
      legend: {
        left: 10,
        selected: {
          // selected'series 1'
          "peptide intensity": true,
          // unselected'series 2'
          "protein intensity": false,
        },
      },
      color: ["rgb(255, 204, 255)"],
      grid: {
        containLabel: true,
        right: "1%",
      },
      tooltip: {
        trigger: "axis",
        axisPointer: {
          type: "shadow",
          label: {
            show: true,
          },
        },
      },
      toolbox: {
        show: true,
        feature: {
          mark: { show: true },
          dataView: { show: true, readOnly: false },
          magicType: { show: true, type: ["line", "bar"] },
          restore: { show: true },
          saveAsImage: { show: true },
        },
      },
      xAxis: {
        type: "category",
        data: category,
        axisLabel: { interval: 0, rotate: 30 },
      },
      yAxis: {},
      series: [
        {
          type: "bar",
          name: "peptide intensity",
          data: data,
        },
        {
          type: "bar",
          name: "protein intensity",
          data: proteinGroupIntensities,
        },
      ],
    };
    this.echartBarplot.setOption(option);
  },
  methods: {
    goToAlignment() {
      this.$emit("callParent");
    },
  },
};
</script>
