<template>
  <div>
    <v-card dark style="margin: 10px">
      <v-card-title>
        <v-btn to="/" icon large color="rgb(255, 204, 255)"><v-icon>mdi-chevron-left</v-icon></v-btn>
        <div>Projekt {{ projectID }}</div>
      </v-card-title>
      <v-card-text>
        <v-simple-table dense dark style="margin: 10px">
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">Publication/Title:</th>
                <th class="text-left">{{ title }}</th>
              </tr>
              <tr>
                <th class="text-left">Tissue:</th>
                <th class="text-left">{{ tissue }}</th>
              </tr>
              <tr>
                <th class="text-left">Instrument:</th>
                <th class="text-left">{{ instrument }}</th>
              </tr>
              <tr>
                <th class="text-left">Affiliation:</th>
                <th class="text-left">{{ university }}</th>
              </tr>
              <tr>
                <th class="text-left">Link:</th>
                <th class="text-left">
                  <a :href="link" target="blank">{{ link }}</a>
                </th>
              </tr>
            </thead>
          </template>
        </v-simple-table>
      </v-card-text>
    </v-card>
      <v-card dark style="margin: 10px">
      <v-card-title>
        List of potential candidates for translational readthrough
      </v-card-title>
      <v-card-text>
        Download
        <v-btn icon @click="downloadExcelTable('candidatesTable', 'candidates_' + projectID)">
                <v-icon>mdi-microsoft-excel</v-icon>
              </v-btn>
      <v-data-table
        dense
        dark
        style="margin: 10px; cursor:pointer;"
        ref="candidatesTable"
        :headers="headers"
        :items="fragment_information"
        :items-per-page="-1"
        :sort-by="['score']"
        :sort-desc="[true]"
        class="elevation-1"
        @click:row="onFragmentRowClicked"
      >
      </v-data-table>
</v-card-text>
    </v-card>
  </div>
</template>

<script>
import {exportExcelTable} from '../exportUtils';

export default {
  data() {
    return {
      headers: [],
      fragment_information: [],
      projectID: String,
      title: String,
      organism: String,
      tissue: String,
      disease: String,
      instrument: String,
      university: String,
      link: String,
    };
  },
  components: {},
  mounted() {
    const projectID = this.$route.params.projectID;
    let project_information = this.$store.state.data[projectID];
    if (project_information === undefined || project_information === null) {
      this.$router.push('/');
      return;
    }
    this.projectID = projectID;
    this.title = project_information.title;
    this.organism = project_information.organism;
    this.tissue = project_information.tissue.replace("[", "").replace("]", "").replaceAll("'","");
    this.disease = project_information.disease;
    this.instrument = project_information.instrument;
    this.university = project_information.university;
    this.link = project_information.link;
    const fragment_information = project_information.fragment_information;

    let tissue = project_information.tissue
          .replace("[", "")
          .replace("]", "")
          .replaceAll("'", "");
    if(tissue == ""){
      tissue = "-";
    }
    for(const fragment of fragment_information) {
          const genename = fragment.genename;
          let offtargets_dict = fragment.offtargets;
          let offtargets = "";
          if (offtargets_dict != "None") {
            let offtargets_list = offtargets_dict.split("}},");
            for (let target of offtargets_list) {
              target = target.replaceAll("{", "").replaceAll("}", "");
              const proteinID = target.split(":")[0];
              if(!offtargets.includes(proteinID)){
                offtargets = offtargets + proteinID + ",";
              }
            }
          }else{
            offtargets = "-";
          }
          const deeploc_info = fragment.deeploc_info;
          let localization = "";
          let signals = "";
          for(let info of deeploc_info){
            if(info[0]['localization']){
              localization =  info[0]['localization'];
            }
            else{
              localization = "-";
            }
            if(info[0]['signals']){
              signals = info[0]['signals'];
            }
            else{
              signals = "-";
            }
          }
          let downstream = fragment.downstream;
          downstream = downstream.slice(0,5);
          let pfam_domain_in_extension = fragment.pfam_domain_in_extension;
          if (pfam_domain_in_extension.size == 0){
            pfam_domain_in_extension = "-";
          }else{
            pfam_domain_in_extension = [...fragment.pfam_domain_in_extension].join(', ');
          }
          this.fragment_information.push({
            id: fragment.id,
            counter: 1,
            projectID: projectID,
            genename: genename,
            protein_description: fragment.protein_description,
            tissue: tissue,
            proteinid: fragment.proteinid,
            score: (fragment.score).toFixed(1),
            sequence_coverage: (fragment.sequence_coverage).toFixed(1),
            offtargets: offtargets,
            peptide: fragment.peptide,
            upstream: fragment.upstream,
            stopcodon: fragment.stopcodon,
            downstream: downstream,
            pfam_domain_in_extension: pfam_domain_in_extension,
            localization: localization,
            signals: signals,
          });
    }

    this.headers = [
      {
        text: "Gene name",
        value: "genename",
      },
      {
        text: "Protein description",
        value: "protein_description"
      },
      {
        text: "ProteinID",
        value: "proteinid",
      },
      {
        text: "Score",
        align: "start",
        sortable: true,
        value: "score",
      },
      {
        text: "Sequence coverage [%]",
        sortable: true,
        value: "sequence_coverage",
      },
      {
        text: "Offtargets",
        sortable: true,
        value: "offtargets",
      },
      {
        text: "Fragment",
        value: "peptide",
      },
      
      {
        text: "Stopcodon",
        value: "stopcodon",
      },
      {
        text: "Downstream",
        value: "downstream",
      },
      {
        text:"Pfam domain in extension",
        value: "pfam_domain_in_extension"
      },
      {
        text: "Localizazion",
        value: "localization"
      },
      {
        text: "Signal",
        value: "signals"
      }
    ];
  },
  methods: {
    onFragmentRowClicked(item) {
      this.$router.push(
        "/fragment/" + this.$route.params.projectID + "/" + item.id
      );
    },
    downloadExcelTable(refName, filename) {
      exportExcelTable(this.$refs[refName].$el, filename);
    }
  },
};
</script>