<template>
  <v-card dark style="margin: 5px">
    <div class="text-no-wrap secondary">
           <v-app-bar
            rounded
            flat
            color="#000000"
          >
            <v-toolbar-title >
              Overview
            </v-toolbar-title>
            <v-spacer></v-spacer>
          </v-app-bar>
        </div>
    <v-card-title></v-card-title>
    <v-card-text>
      The overview shows the collected data of all prediction tools. By selecting Pfam-, Blast- or Phobius-regions you can jump to the detailed results on this page. 
      The red bubble defines the overwritten stopcodon. Peptides are shown as alignment to the whole protein sequence, where the pink one marks the found fragment in the extension. 
      The curves of Phobius results show the probalbilities for e.g. signal peptides or transmembrane regions. 
    </v-card-text>
    <v-container fluid>
      <v-row no-gutters>
        <v-col>
          <div id="featureView1"></div>
        </v-col>
      </v-row>
    </v-container>
  </v-card>
</template>

<script>
import { FeatureViewer } from "feature-viewer-typescript";


export default {
  data() {
    return {
      option: {},
      boardWidth: "100%",
      boardHeight: 400,
    };
  },
  components: {},
  mounted() {
    const projectID = this.$route.params.projectID;
    let project_information = this.$store.state.data;
    const project = project_information[projectID];
    const list_of_info = project.fragment_information;
    const id = parseInt(this.$route.params.id);
    const entry = list_of_info.find((x) => x.id === id);
    const whole_sequence = entry["alignment"].split("\n")[0];
    const peptide_sequences_string = entry["peptide_sequences"];
    let peptide_sequences = peptide_sequences_string.split(";");
    const sequence_length = whole_sequence.length;
    this.boardWidth = parseInt(sequence_length) + 20;
    const extension = entry["extension_sequence"].length;
    const stop = sequence_length - extension;
    let peptideX = entry["peptide"];
    const start_index_peptideX = whole_sequence.indexOf(peptideX) +1;
    const pfam_info = entry.pfam_info;
    let pfam_domains = [];
    let found_pfam_hits = [];
    for (let result of pfam_info) {
      if (result.length != 0) {
        const name = result[1];
        const accession = result[2];
        const startposition = parseInt(result[7]);
        const endposition = parseInt(result[8]);
        if (!found_pfam_hits.includes((startposition, endposition))) {
          found_pfam_hits.push((startposition, endposition));
          pfam_domains.push({
            x: startposition,
            y: endposition,
            tooltip: '<p style="display: inline-block;"> ' + name + " (" + accession + ")" + "</p>",
            accession: accession,
            label:  name,
            className: "myButton", //can be used for styling
          });
        }
      }
    }

    let peptide_data = [];
    for (let peptide of peptide_sequences) {
      const start_index = whole_sequence.indexOf(peptide) +1;
      const end_index = start_index + peptide.length;
      if (start_index == start_index_peptideX) {
        peptide_data.push({
          x: start_index,
          y: end_index,
          tooltip: peptide,
          color: "#FFCCFF",
        });
      } else {
        peptide_data.push({
          x: start_index,
          y: end_index,
          tooltip: peptide,
        });
      }
    }
    const blast_hits = entry["blast_info"];
    let blast_data = [];
    let found_blast_hits = [];
    for (let result_list of blast_hits) {
      for (let hit of result_list) {
        if (hit.length != 0) {
          const hitID = hit[0];
          const name = hit[2];
          const start_position = hit[10];
          const match = hit[7].split(" ")[1].split(".")[0];
          const start_index = whole_sequence.indexOf(match);
          const end_index = start_index + match.length;
          if (!isNaN(start_position)) {
            if (start_position != "") {
              if (!found_blast_hits.includes((hitID, name))) {
                found_blast_hits.push((hitID, name));
                blast_data.push({
                  x: start_index,
                  y: end_index,
                  tooltip: '<p style="display: inline-block;"> ' + name + " (" + hitID + ")"+ "</p>",
                });
              }
            }
          }
        }
      }
    }
    const phobius_info = entry.phobius_info;
    const cytoplasmic = [];
    const non_cytoplasmic = [];
    const transmembrane = [];
    const signalpeptide = [];
    let x_value = 1;
    for (let x of phobius_info) {
      cytoplasmic.push({ x: x_value, y: 100 * x[2] });
      non_cytoplasmic.push({ x: x_value, y: 100 * x[3] });
      transmembrane.push({ x: x_value, y: 100 * x[4] });
      signalpeptide.push({ x: x_value, y: 100 * x[5] });
      x_value += 1;
    }
    let deeploc_probabilities = entry.deeploc_probabilities;
    let myCurve = [];
    //let myCurve2 = {};
    let prob_info = [];
    let x_prob = 1;
    deeploc_probabilities = deeploc_probabilities.slice(1);
    if(sequence_length > 1022){
      let deeploc_probabilities_part_1 = deeploc_probabilities.slice(0, 512);
      for (let y_prob of deeploc_probabilities_part_1){
        prob_info.push({x: x_prob, y: y_prob[1]});
        x_prob +=1;
      }
      let deeploc_probabilities_part_2 = deeploc_probabilities.slice(511, sequence_length);
      let prob_info2 = [];
      let x_prob2 = sequence_length - 511;
      for (let y_prob of deeploc_probabilities_part_2){
        prob_info2.push({x: x_prob2, y: y_prob[1]});
        x_prob2 +=1;
      }
      myCurve = [{ type: "curve", id: "mycurve5", label:'<span style="color: #000000;">DeepLoc n-term</span>', data: prob_info, height: 1, yLim: 200, color: "#BA55D3"},{type: "curve", id: "mycurve6", label:'<span style="color: #000000;">DeepLoc c-term</span>', data: prob_info2, height: 1, yLim: 200, color: "#BA55D3"}];
      
    }else{
      for (let y_prob of deeploc_probabilities){
        prob_info.push({x: x_prob, y: y_prob[1]}); 
        x_prob +=1;
      }
      myCurve = [{ type: "curve", id: "mycurve5", label:'<span style="color: #000000;">DeepLoc</span>', data: prob_info, height: 1, yLim: 200, color: "#BA55D3"}];
    }
    

    const viewer = new FeatureViewer(whole_sequence, "#featureView1", {
      toolbar: true,
      tooltip: true,
      toolbarPosition: "right",
      brushActive: true,
      sideBar: false,
      zoomMax: 10,
      flagTrack: 230,
      flagColor: "#FFFFFF",
      flagTrackMobile: 100,
      backgroundcolor: '#404040',
      showSequence: true,
    });
    viewer.onRegionSelected((e) => {
      if(e.detail.label === '<span style="color: #000000;">Peptides</span>'){
        this.$emit("callParent", "peptide_barchart");
      } else if (e.detail.label === '<span style="color: #000000;">Pfam</span>') {
        this.$emit("callParent", "pfam_table");
      } else if (
        e.detail.label === '<span style="color: #000000;">BLASTp</span>'
      ) {
        this.$emit("callParent", "blast_table");
      } else if (
        e.detail.label ===
          '<span style="color: #000000;">Phobius Signalpeptide</span>' ||
        e.detail.label ===
          '<span style="color: #000000;">Phobius Transmembrane</span>' ||
        e.detail.label ===
          '<span style="color: #000000;">Phobius Cytoplasmic</span>' ||
        e.detail.label ===
          '<span style="color: #000000;">Phobius Non cytoplasmic</span>'
      ) {
        this.$emit("callParent", "phobius_table");
      } else if (
        e.detail.label === '<span style="color: #000000;">DeepLoc</span>' || 
        e.detail.label === '<span style="color: #000000;">DeepLoc n-term</span>' ||
        e.detail.label === '<span style="color: #000000;">DeepLoc c-term</span>'
      ) {
        this.$emit("callParent", "deeploc_table");
      }
    });

    let list_features=[
      {
        type: "lollipop",
        id: "extension",
        label: '<span style="color: #000000;">Extension</span>',
        data: [{ x: stop, y: 1, tooltip: 'Overwritten stopcodon (' + stop.toString() + ")" }],
        color: "#CD0000",
        height: 10,
      },
      {
        type: "rect",
        id: "peptides",
        label: '<span style="color: #000000;">Peptides</span>',
        data: peptide_data,
      },
      {
        type: "rect",
        id: "blast",
        label: '<span style="color: #000000;">BLASTp</span>',
        data: blast_data,
        color: "#6495ED",
      },
      {
        type: "rect",
        id: "pfam",
        label: '<span style="color: #000000;">Pfam</span>',
        data: pfam_domains,
        color: "#FF7F24",
      },
      {
        type: "curve",
        id: "mycurve",
        label: '<span style="color: #000000;">Phobius Signalpeptide</span>',
        data: signalpeptide,
        height: 1,
        yLim: 200,
        color: "#FF0087",
      },
      {
        type: "curve",
        id: "mycurve2",
        label: '<span style="color: #000000;">Phobius Transmembrane</span>',
        data: transmembrane,
        height: 1,
        yLim: 200,
        color: "#80FFA5",
      },
      {
        type: "curve",
        id: "mycurve3",
        label: '<span style="color: #000000;">Phobius Cytoplasmic</span>',
        data: cytoplasmic,
        height: 1,
        yLim: 200,
        color: "#FFBF00",
      },
      {
        type: "curve",
        id: "mycurve4",
        label: '<span style="color: #000000;">Phobius Non cytoplasmic</span>',
        data: non_cytoplasmic,
        height: 1,
        yLim: 200,
        color: "#00DDFF",
      }]
    list_features=list_features.concat(myCurve)
    viewer.addFeatures(list_features);
    this.$nextTick(() => viewer.resizeViewer());
  },
};
</script>

<style>
@keyframes loadingcolorflag {
  50% {
    fill: gray;
  }
}
foreignObject {
  color: #000;
}
foreignObject > body {
  overflow: visible;
}
.position-label text{
  color: gray;
}
#zoomPosition {
  color: gray;
}
.zoom-label text{
  color: gray;
}
.header-zoom span{
  color: gray;
}
.helperButton {
  width: 20px;
  height: 20px;
}
.helperButton path {
  fill: gray;
}
.selectionRect {
  fill: rgba(0, 0, 0, 0.15);
  fill-opacity: 0.5;
}
.brush .selection {
  stroke: #fff;
  fill: #4682b4;
  fill-opacity: 0.125;
  shape-rendering: crispEdges;
  display: block;
  height: 100%;
}
.mybutton {
  -moz-border-radius: 10px;
  -webkit-border-radius: 10px;
  border-radius: 10px;
  -khtml-border-radius: 10px;
  background: #a9a9a9;
  border: none;
  color: #000;
  height: 30px;
  min-width: 36px;
  outline: none;
  text-decoration: none;
  text-align: center;
  line-height: 24px;
  vertical-align: middle;
  overflow: visible !important;
}
.mybuttonsquare {
  border-radius: 0px;
  font-size: 12px;
  height: 30px;
  cursor: default;
  margin: auto;
  min-width: 30px;
  width: 70px;
  font-family: "Roboto";
  font-size: 10px;
  background: #fff;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.12), 0 2px 2px 0 rgba(0, 0, 0, 0.24);
  line-height: normal;
}
.mybuttoncircle {
  border-radius: 50%;
  font-size: 12px;
  height: 36px;
  width: 36px;
  cursor: pointer;
  background: #fff;
  line-height: normal;
}
.axis {
  font: 14px sans-serif;
}
.Arrow {
  fill-opacity: 0.6;
}
.fvtooltip {
  position: absolute;
  text-align: center;
  color: #fff;
  width: auto;
  height: auto;
  padding: 2px;
  font-family: "Arial", sans-serif;
  font-size: 10px;
  font-weight: normal;
  background-color: rgb(160, 160, 160);
  border: 0px;
  border-radius: 4px;
  pointer-events: none;
}
.fvcustomtooltip {
  position: absolute;
  text-align: center;
  color: #fff;
  height: auto;
  background-color: rgb(160, 160, 160);
  pointer-events: none;
}
.pageoverlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 2;
  cursor: pointer;
}
</style>