<template>
  <v-app style="background-color: rgb(255, 204, 255)">
    <v-app-bar
      color="rgb(0, 0, 0)"
      dark
      shrink-on-scroll
      fade-img-on-scroll
      app
    >
      <template v-slot:img="{ props }" v-if="showAnimation">
        <AppBarAnimation v-bind="props" />
      </template>
      <v-app-bar-title>
        <v-img src="data:image/png;base64," height="100%" contain max-height="150"></v-img>
      </v-app-bar-title>
    </v-app-bar>
    <v-main>
      <router-view />
    </v-main>
  </v-app>
</template>

<script>
import AppBarAnimation from "@/components/AppBarAnimation.vue";

export default {
  components: { AppBarAnimation },
  computed: {
    showAnimation() {
      return (
        this.$route.name === "Home" &&
        Object.keys(this.$store.state.data).length === 0
      );
    },
  },
};
</script>

