import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    data: {
      // dictionary of project id => project data
    }
  },
  mutations: {
    storeProject(state, project) {
      //console.log('Store project with ID', project.projectID, project);
      Vue.set(state.data, project.projectID, project);
    }
  },
  actions: {
    processProject(context, projectData) {
      const projectID = Object.keys(projectData)[0];
      //console.log('Process project with ID', projectID, projectData);
      const list_of_info = [];
      let id = 1;

      const fragment_information = projectData[projectID].fragment_information;
      for (let fragment in fragment_information) {
        const candidate = fragment_information[fragment];
        let score_int = fragment_information[fragment].score;
        let sequ_cov_int = parseFloat((fragment_information[fragment].sequenceCoverage).replace(',', '.'));
        let offtargets = candidate.offtargets.replace("[", "").replace("]", "").replaceAll("'","");
        if(offtargets == ""){
          offtargets = "None"
        }
        let protein_description = (candidate.proteinName.split("[")[0]).split("|")[1];
        let pfam_info = candidate.pfam_info;
        let extension = candidate.extensionSequence;
        let extension_length = extension.length;
        let sequence_length_with_ext = candidate.sequenceLength;
        const sequence_length = sequence_length_with_ext - extension.length;
        let domain_in_extension = "";
        let pfam_domain_in_extension = new Set();
        for (let result of pfam_info) {
            if (result.length != 0) {
              const startposition = result[7];
              const endposition = result[8];
              
              if (
                sequence_length < startposition &&
                sequence_length < endposition
              ) {
                domain_in_extension = "in extension";
                pfam_domain_in_extension.add(domain_in_extension);
              }
              else if (
                sequence_length > startposition &&
                sequence_length < endposition
              ) {
                domain_in_extension = "overlap";
                pfam_domain_in_extension.add(domain_in_extension);
              }else{
                domain_in_extension = "-";
              }
              result.push(domain_in_extension);
            }
          
        }
        let phobius_info = candidate.phobius_info;
        let phobius_in_extension = new Set();
        let mean_native_cytoplasmic = 0;
        let mean_native_non_cytoplasmic = 0;
        let mean_native_transmembrane = 0;
        let mean_native_signalpeptide = 0;
        for (let i = 0; i < sequence_length; i++) {
          mean_native_cytoplasmic += parseFloat(phobius_info[i][2]);
          mean_native_non_cytoplasmic += parseFloat(phobius_info[i][3]);
          mean_native_transmembrane += parseFloat(phobius_info[i][4]);
          mean_native_signalpeptide += parseFloat(phobius_info[i][5]);
        } 
        mean_native_cytoplasmic = (mean_native_cytoplasmic / sequence_length) * 100;
        mean_native_non_cytoplasmic = (mean_native_non_cytoplasmic / sequence_length) * 100;
        mean_native_transmembrane = (mean_native_transmembrane / sequence_length) * 100;
        mean_native_signalpeptide = (mean_native_signalpeptide/ sequence_length) * 100;
        let mean_extension_cytoplasmic = 0;
        let mean_extension_non_cytoplasmic = 0;
        let mean_extension_transmembrane = 0;
        let mean_extension_signalpeptide = 0;
        for (let i = sequence_length; i < sequence_length_with_ext; i++){
          mean_extension_cytoplasmic += parseFloat(phobius_info[i][2]);
          mean_extension_non_cytoplasmic += parseFloat(phobius_info[i][3]);
          mean_extension_transmembrane += parseFloat(phobius_info[i][4]);
          mean_extension_signalpeptide += parseFloat(phobius_info[i][5]);
        }
        mean_extension_cytoplasmic = (mean_extension_cytoplasmic /extension_length) * 100;
        mean_extension_non_cytoplasmic = (mean_extension_non_cytoplasmic /extension_length) * 100;
        mean_extension_transmembrane = (mean_extension_transmembrane /extension_length) * 100;
        mean_extension_signalpeptide = (mean_extension_signalpeptide /extension_length) * 100;
        const difference_cytoplasmic = mean_native_cytoplasmic - mean_extension_cytoplasmic;
        if (difference_cytoplasmic < 0 ){
          if ((difference_cytoplasmic * (-1)) > 25){
            phobius_in_extension.add("cytoplasmic");
          }
        }
        const difference_non_cytoplasmic = mean_native_non_cytoplasmic - mean_extension_non_cytoplasmic;
        if (difference_non_cytoplasmic < 0 ){
          if ((difference_non_cytoplasmic * (-1)) > 25){
            phobius_in_extension.add("non cytoplasmic");
          }
        }
        const difference_transmembrane = mean_native_transmembrane - mean_extension_transmembrane;
        if (difference_transmembrane < 0 ){
          if ((difference_transmembrane * (-1)) > 25){
            phobius_in_extension.add("transmembrane");
          }
        }
        const difference_signalpeptide = mean_native_signalpeptide - mean_extension_signalpeptide;
        if (difference_signalpeptide < 0){
          if (((difference_signalpeptide * (-1))) > 25){
            phobius_in_extension.add("signalpeptide");
          }

        }
        list_of_info.push({
          id: id,
          score: score_int,
          peptide: candidate.peptide,
          proteinid: fragment,
          sequence_coverage: sequ_cov_int,
          qvalue: candidate.qvalue,
          molecular_weightkda: candidate.molecularWeight,
          sequence_lengthaa: candidate.sequenceLength,
          protein_name: candidate.proteinName,
          versions: candidate.versions,
          upstream: candidate.upstream,
          stopcodon: candidate.stopcodon,
          downstream: candidate.downstream,
          downstream_position_on_chromosome:
            candidate.downstreamPositionOnChromosome,
          proteingroup_score: candidate.proteingroupScore,
          delta_score: candidate.deltaScore,
          missed_cleavages: candidate.missedCleavages,
          mass_errorppm: candidate.massErrorPPM,
          mass_errorda: candidate.massErrorDA,
          uncalibrated_mass_errorppm: candidate.uncalibratedMassErrorPPM,
          uncalibrated_mass_errorda: candidate.uncalibratedMassErrorDA,
          msms_scan_number: candidate.msmsScanNumber,
          upstream_nterm_cleavage_window: candidate.upstreamNtermCleavageWindow,
          downstream_cterm_cleavage_window:
            candidate.downstreamCtermCleavageWindow,
          extension_sequence: candidate.extensionSequence,
          genename: candidate.geneName,
          geneid: candidate.geneID,
          sequenceA: candidate.sequenceA,
          alignment: candidate.alignment,
          offtargets: offtargets,
          intensities: candidate.intensities,
          no_of_peptides: candidate.noOfPeptides,
          peptide_sequences: candidate.peptide_sequences,
          blast_info: candidate.blast_info,
          pfam_info: pfam_info,
          phobius_info: candidate.phobius_info,
          deeploc_png: candidate.deeploc_png,
          deeploc_info: candidate.deeploc_info,
          proteinGroup_info : candidate.proteinGroup_info,
          evidence_info: candidate.evidence_info,
          peptide_info: candidate.peptide_info,
          protein_description: protein_description,
          deeploc_probabilities: candidate.deeploc_probabilities,
          pfam_domain_in_extension: pfam_domain_in_extension,
          phobius_in_extension: phobius_in_extension,
        });
        id += 1;
      }
      let ldhb_found = projectData[projectID].ldhb_found;
      let mdh1_found = projectData[projectID].mdh1_found;
      //console.log(ldhb_found, typeof ldhb_found);
      if (ldhb_found == true) {
        ldhb_found = "found";
      } else {
        ldhb_found = "-";
      }
      if (mdh1_found == true) {
        mdh1_found = "found";
      } else {
        mdh1_found = "-";
      }
      let disease =  projectData[projectID].disease;
      if (disease == "None"){
        disease = "-";
      }
      const result = {
        "projectID": projectID,
        "title": projectData[projectID].title,
        "organism": projectData[projectID].organism,
        "tissue":  projectData[projectID].tissue,
        "disease": disease,
        "instrument": projectData[projectID].instrument,
        "university": projectData[projectID].university,
        "link": projectData[projectID].link,
        "no_of_experiments": projectData[projectID].no_of_experiments,
        "ldhb_found": ldhb_found,
        "mdh1_found": mdh1_found,
        "percentage_of_tga": projectData[projectID].percentage_of_tga,
        "fragment_information": list_of_info,
        "no_all_proteins": projectData[projectID].no_all_proteins,
      };
      context.commit('storeProject', result);
    }
  }
})
